import { mapActions, mapState } from 'vuex'
import MeList from '../../components/meListTemplate'
import SuspendPlay from '../../components/suspendPlayTemplate'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      lists: [
        {
          icon: upyunUrl + '/wxXcxImg/images/my-list-icon1.png',
          title: '我的订单',
          url: 'myOrder/index?status=true',
          isLogin: true
        },
        {
          icon: upyunUrl + '/wxXcxImg/images/my-list-icon2.png',
          title: '我的课程',
          url: 'myCourse/index',
          isLogin: true
        },
        {
          icon: upyunUrl + '/wxXcxImg/images/my-list-icon3.png',
          title: '我的能量',
          url: 'meEnergy/index',
          isLogin: true
        },
        {
          icon: upyunUrl + '/wxXcxImg/images/my-list-icon4.png',
          title: '内容定制',
          url: 'contentCustom/index',
          isLogin: true
        },
        {
          icon: upyunUrl + '/wxXcxImg/images/my-list-icon5.png',
          title: '意见反馈',
          url: 'feedback/index',
          isLogin: true
        },
        {
          icon: upyunUrl + '/wxXcxImg/images/my-list-icon6.png',
          title: '瓶盖成长记录',
          url: 'growthRecord/index',
          isLogin: false
        },
        {
          icon: upyunUrl + '/wxXcxImg/images/my-list-icon7.png',
          title: '申请讲师入驻',
          url: 'applyEnter/index',
          isLogin: false
        },
        {
          icon: upyunUrl + '/wxXcxImg/images/my-list-icon7.png',
          title: '退出登录',
          url: '',
          isLogin: false
        }
      ],
      freePopupState: false,
      getFree: '',
      cancelFree: '',
      noReadCount: 0
    }
  },
  onLoad () {
    this.initializeFn()
  },
  onPullDownRefresh () {
    this.initializeFn()
  },
  computed: {
    ...mapState([
      'userInfo',
      'musicList'
    ])
  },
  methods: {
    ...mapActions([
      'getUserInfo',
      'getNoReadCount',
      'getFreeSubject',
      'cancelFreeSubject'
    ]),
    initializeFn () {
      this.getUserInfoFn()
      this.getNoReadCountFn()
      this.getFreeSubjectFn()
    },
    // 用户收藏播放点赞统计
    getUserInfoFn () {
      if (this.userInfo) {
        this.getUserInfo().then(() => {
          uni.stopPullDownRefresh()
        })
      }
    },
    // 获取未读总数
    getNoReadCountFn () {
      if (this.userInfo) {
        this.getNoReadCount({ that: this }).then((res) => {
          this.noReadCount = res
        })
      }
    },
    // 显示免费领取课程
    getFreeSubjectFn () {
      if (this.userInfo) {
        this.getFreeSubject({ that: this }).then((res) => {
          this.getFree = res
        })
      }
    },
    // 关闭免费领取课程弹窗
    onCloseBoxFn (status) {
      this.freePopupState = status
    },
    // 取消免费领取课程操作
    cancelFreeFn (id) {
      if (this.userInfo) {
        uni.showLoading()
        this.cancelFreeSubject({ that: this, id: id }).then(() => {
          this.getFree['status'] = 2
          this.onCloseBoxFn(false)
          uni.hideLoading()
        })
      }
    },
    // 跳转页面
    onSkipPageFn (url) {
      if (this.userInfo) {
        uni.navigateTo({
          url: url
        })
      } else {
        uni.navigateTo({
          url: `/pages/login/index`
        })
      }
    }
  },
  components: {
    MeList,
    SuspendPlay
  }
}
